import React from 'react';

import './Portfolio.scss';
import {PortfolioSlider} from './PortfolioSlider'
import image1 from '../../assets/images/portfolio/vertical/image-1.png';
import image2 from '../../assets/images/portfolio/vertical/image-2.png';
import image3 from '../../assets/images/portfolio/vertical/image-3.png';
import image4 from '../../assets/images/portfolio/vertical/image-4.png';
import image5 from '../../assets/images/portfolio/vertical/image-5.png';
import image6 from '../../assets/images/portfolio/vertical/image-6.png';
import image7 from '../../assets/images/portfolio/vertical/image-7.png';
import image8 from '../../assets/images/portfolio/vertical/image-8.png';
import image9 from '../../assets/images/portfolio/vertical/image-9.png';
import image10 from '../../assets/images/portfolio/vertical/image-10.png';
const PORTFOLIO_IMAGES = [image1, image2, image3, image4, image5, image6, image7, image8, image9, image10];

const Portfolio = () => (
    <div className='portfolio | section-wrapper background-neutral-900'>
        <div className='content-wrapper'>
            <div className='portfolio__header | section-header text-neutral-100'>
                Портфолйо
            </div>
            <div className='portfolio__body'>
                <img className="portfolio__image portfolio__image-1"
                     src={image1} alt=''/>
                <img className="portfolio__image portfolio__image-2"
                     src={image2} alt=''/>
                <img className="portfolio__image portfolio__image-3"
                     src={image3} alt=''/>
                <img className="portfolio__image portfolio__image-4"
                     src={image4} alt=''/>
                <img className="portfolio__image portfolio__image-5"
                     src={image5} alt=''/>
                <img className="portfolio__image portfolio__image-6"
                     src={image6} alt=''/>
                <img className="portfolio__image portfolio__image-7"
                     src={image7} alt=''/>
                <img className="portfolio__image portfolio__image-8"
                     src={image8} alt=''/>
                <img className="portfolio__image portfolio__image-9"
                     src={image9} alt=''/>
                <img className="portfolio__image portfolio__image-10" alt=''/>
                <div className='portfolio__slider'>
                    <PortfolioSlider imageUrls={PORTFOLIO_IMAGES}/>
                </div>
            </div>
        </div>
    </div>
);

export default Portfolio;