import React, { useState } from "react";
import {motion, useMotionValue} from "framer-motion";
import './PortfolioSlider.scss';

const DRAG_BUFFER = 24;
export function PortfolioSlider({ imageUrls = [] }) {
    const [imageIndex, setImageIndex] = useState(0);
    const [dragging, setDragging] = useState(false);
    const dragX = useMotionValue(0);
    const onDragStart = () => {
        setDragging(true);
    }
    const onDragEnd = () => {
        setDragging(false);
        if (dragX.get() <= -DRAG_BUFFER)
            setImageIndex(index => {return (index === imageUrls.length - 1) ? imageUrls.length - 1 : index + 1;});
        else if (dragX.get() >= DRAG_BUFFER)
            setImageIndex(index => {return (index === 0) ? 0 : index - 1;})
    }
    return (
        <div className='slider__images'>
            {imageUrls.map((url, index) => (
                <motion.img key={index} src={url} alt=''
                            style={{
                                translate: `${13.66-100 * imageIndex}%`,
                                x: dragX
                            }}
                            drag={"x"}
                            dragConstraints={{
                                left: 0,
                                right: 0
                            }}
                            onDragStart={onDragStart}
                            onDragEnd={onDragEnd}
                            animate={{
                                scale: imageIndex === index ? 1 : 0.8
                            }}
                            transition={{
                                type: "spring",
                                mass: 3,
                                stiffness: 400,
                                damping: 50
                            }}/>
            ))}
        </div>
    );
}
